<template>
  <div id="app">
    <div class="container" v-if="!closed">
      <div class="close" @click="close()"><h1 class="x">x</h1></div>
      <div class="inner-container">
        <h1 class="heading">Garage<br />Corleone</h1>
        <img
          class="logo"
          src="https://cdn.discordapp.com/attachments/863492134656147478/864667230057529384/Logo_Transparent.png"
          alt="logo"
        />
        <ul class="vehicles" v-if="!parking">
          <li
            v-for="(vehicle, index) in vehicles"
            :key="index"
            :class="`vehicle row-${index > 2 ? (index <= 3 ? 2 : 3) : 1}`"
          >
            <div class="vehicle-div" @click="park(vehicle.plate, false)">
              <img class="vehicle-img" src="car.png" alt="car.svg" />
              <p class="veh-name">{{ vehicle.name }}</p>
              <p class="veh-plate">{{ vehicle.plate }}</p>
            </div>
            <div class="edit-icon-container" @click="edit(index)">
              <img class="edit-icon" src="pen.svg" alt="pen.svg" />
            </div>
          </li>
        </ul>
        <ul class="vehicles" v-else>
          <li
            v-for="(vehicle, index) in parkVehs"
            :key="index"
            :class="`vehicle row-${index > 2 ? (index <= 3 ? 2 : 3) : 1}`"
          >
            <div class="vehicle-div" @click="park(vehicle.plate, true)">
              <img class="vehicle-img" src="car.png" alt="car.svg" />
              <p class="veh-name">{{ vehicle.name }}</p>
              <p class="veh-plate">{{ vehicle.plate }}</p>
            </div>
            <div class="edit-icon-container" @click="edit(index)">
              <img class="edit-icon" src="pen.svg" alt="pen.svg" />
            </div>
          </li>
        </ul>
        <div class="arrows">
          <div class="arrow-out" @click="changeSite(false)">AUSPARKEN</div>
          <div class="arrow-in" @click="changeSite(true)">EINPARKEN</div>
        </div>
      </div>
    </div>
    <div v-if="editing > -1" class="editor">
      <div class="editor-window">
        <h2 class="edit-heading">
          Name ändern von<br />{{ vehicles[editing].name }}
        </h2>
        <input type="text" v-model="newName" placeholder="Neuer Name" />
        <div class="btn-cancel" @click="editing = -1">
          <p>x</p>
        </div>
        <div class="btn-submit" @click="submit()">
          <p>Bestätigen</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      closed: true,
      newName: "",
      editing: -1,
      sites: 0,
      parking: false,
      vehicles: [],
      parkVehs: [],
    };
  },
  methods: {
    changeSite(parking) {
      this.parking = parking;
      if (parking) {
        fetch("https://cc_garage/enable-parking", {
          method: "post",
        });
        this.vehicles = [];
        this.parkVehs = [];
      } else {
        fetch("https://cc_garage/enable-parkout", {
          method: "post",
        });
        this.vehicles = [];
        this.parkVehs = [];
      }
    },
    getVehicles(vehs) {
      for (let i = 0; i < vehs.length / 9; i++) {
        this.sites++;
      }
      this.vehicles = vehs;
    },
    edit(index) {
      this.editing = index;
    },
    submit() {
      this.vehicles[this.editing].name = this.newName;
      fetch("https://cc_garage/new-name", {
        method: "post",
        body: JSON.stringify({
          plate: this.vehicles[this.editing].plate,
          newName: this.newName,
        }),
      });
      this.editing = -1;
      this.newName = "";
    },
    close() {
      this.closed = true;
      this.vehicles = [];
      this.parkVehs = [];
      this.parking = false;
      fetch("https://cc_garage/escape", {
        method: "post",
      });
    },
    park(plate, parkIn) {
      console.log("close");
      if (parkIn) {
        fetch("https://cc_garage/park-in", {
          method: "post",
          body: JSON.stringify({
            plate: plate,
          }),
        });
      } else {
        fetch("https://cc_garage/park-out", {
          method: "post",
          body: JSON.stringify({
            plate: plate,
          }),
        });
      }
      this.close();
    },
  },
  mounted() {
    // this.vehicles.push({
    //   name: "Zentorno",
    //   plate: "SFS 235",
    // });
    window.addEventListener("message", (e) => {
      let data = e.data;
      switch (data.action) {
        case "open":
          fetch("https://cc_garage/enable-parkout", {
            method: "post",
          });
          // this.getVehicles(data.vehicles);
          this.closed = false;
          break;
        case "close":
          this.closed = true;
          break;
        case "add":
          this.vehicles.push({
            name: data.name,
            plate: data.plate,
          });
          break;
        case "add-p":
          this.parkVehs.push({
            name: data.name,
            plate: data.plate,
          });
          break;
      }
    });
  },
};
</script>

